@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans:wght@300&display=swap);
* {
  margin: 0px;
  padding: 0px;
}
#baremenu {
  z-index: 1;
  position: sticky;
  top: 0px;
  color: white;
  background-color: #303a5f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem;
}

#inputSearch {
  outline: none;
  padding: 5px;
  font-size: inherit;
  background-color: inherit;
  color: inherit;
  border: 1px solid #272735;
  border-radius: 10px;
}

body {
  background-color: #272735;
}
#movies {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.item {
  position: relative;
  margin: 1rem;
  cursor: pointer;
  overflow: hidden;

  width: 20rem;
  border: 1px solid #303a5f;
  border-radius: 0.3rem;
}
.item-movie {
  color: white;
}
.overview {
  position: absolute;
  top: -100%;
  left: 0;
  right: 0;
  background-color: white;
  padding: 1rem;
  /* width: 100%; */
}
:where(.overview) h3 {
  margin-bottom: 1rem;
}
.overview p {
  height: 12vh;
  overflow: auto;
}
.overview p::-webkit-scrollbar {
  width: 0.2rem;
  background-color: #ddd;
}
.overview h4 {
  margin: 10px 0px;
}
.item:hover .overview {
  transition: all 0.5s linear;
  top: 0;
}
.item-movie img {
  width: 100%;
  height: 20rem;
  object-fit: contain;
}
.item-movie h4 {
  /* background-color: red; */

  white-space: nowrap;
  text-overflow: ellipsis;
  width: 90%;
  overflow: hidden;

  padding: 0.3rem;
}
.item-movie-info {
  display: flex;
  justify-content: space-between;
  background-color: #303a5f;
  padding: 10px;
}

.vote1 {
  background-color: rgb(138, 180, 71);
  padding: 0.1rem 1.5rem;
  border-radius: 0.3rem;
}

.vote2 {
  background-color: rgb(163, 93, 81);
  padding: 0.1rem 1.5rem;
  border-radius: 0.3rem;
}

#MovieApp {
  font-family: "Courier New", Courier, monospace;
}

.loading {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;
}

.loading div {
  margin: 1rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.7rem solid transparent;
  padding: 0.5rem;

  border-top-color: #303a5f;
  border-bottom-color: #303a5f;
  -webkit-animation: load 1s ease-in-out infinite;
          animation: load 1s ease-in-out infinite;
}

@-webkit-keyframes load {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#footer {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;

  display: flex;
  justify-content: space-between;
  align-items: center;

  background-color: #303a5f;
  padding: 0.3rem 1rem;
  color: white;
}

.pagination {
  display: inline;
  background-color: #303a5f;
  cursor: pointer;
  padding: 0rem 1rem;
}
.pagination.right {
  right: 0;
}

.pagination.left {
  left: 0;
  border-right: 1px solid #eee;
}

* {
	/* font-weight: bold; */
	font-family: 'Montserrat';
	/*font-family: 'Open Sans', sans-serif;*/
}
